<template>
  <div id="featuresDetails">
    <div class="tab">
      <button
        :class="'tablinks ' +classActive[0]"
        @click="showTabContent('description')"
      >Descripción</button>

      <button
        :class="'tablinks ' +classActive[1]"
        @click="showTabContent('features')"
      >Características</button>

      <button :class="'tablinks ' +classActive[2]" @click="showTabContent('additional')">Adicionales</button>
    </div>

    <!-- Tab content -->
    <div class="tabcontent" v-show="showDescription">
      <p>{{ product.description }}</p>
    </div>

    <div class="tabcontent" v-show="showFeatures">
      <div class="featuresDetails">
        <p>
          <b>Código</b>
        </p>
        <p>{{ product.code }}</p>
        <p>
          <b>ID del producto</b>
        </p>
        <p>{{ product._id }}</p>
      </div>

      <div class="featuresDetails">
        <p>
          <b>Precio x Kg</b>
        </p>
        <p>{{ product.priceKg | priceFormat }}</p>

        <p>
          <b>Peso</b>
        </p>
        <p>{{ product.price / product.priceKg }} Kg</p>

        <p>
          <b>Precio total</b>
        </p>
        <p>{{ product.price | priceFormat }}</p>
      </div>

      <div class="featuresDetails">
        <p>
          <b>Categoría</b>
        </p>
        <p>{{ product.category }}</p>
        <p>
          <b>Sub categoría</b>
        </p>
        <p>{{ product.subCategory }}</p>
      </div>

      <div class="featuresDetails">
        <p>
          <b>Comercializadora</b>
        </p>
        <p>{{ product.company }}</p>
        <p>
          <b>Productor</b>
        </p>
        <p>{{ product.manufacturer }}</p>
      </div>

      <div class="featuresDetails">
        <p>
          <b>Visitas</b>
        </p>
        <p>{{ product.details.visits }}</p>
        <p>
          <b>Vendidos</b>
        </p>
        <p>{{ product.details.sold }}</p>
      </div>
    </div>

    <div class="tabcontent" v-show="showAdditional">
      <p v-show="product.additional.length === 0">No hay información adicional</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "FeaturesDetails",
  props: { product: { type: Object, required: true } },
  data() {
    return {
      classActive: [],
      showDescription: true,
      showFeatures: false,
      showAdditional: false
    };
  },
  mounted() {
    this.showTabContent("description");
  },
  methods: {
    showTabContent(tab) {
      switch (tab) {
        case "description":
          this.showDescription = true;
          this.showFeatures = false;
          this.showAdditional = false;
          this.classActive = ["active", "", ""];
          break;
        case "features":
          this.showDescription = false;
          this.showFeatures = true;
          this.showAdditional = false;
          this.classActive = ["", "active", ""];
          break;
        case "additional":
          this.showDescription = false;
          this.showFeatures = false;
          this.showAdditional = true;
          this.classActive = ["", "", "active"];
          break;
      }
    }
  }
};
</script>

<style>
#featuresDetails {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: auto;
  margin-top: 40px;
  margin-bottom: 40px;
}

/* ****************** Tab ****************/
#featuresDetails .tab {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  width: 80%;
  overflow: hidden;
  border-bottom: 1px solid #ccc;
}
#featuresDetails .tab button {
  height: 50px;
  padding: 0 16px;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  border: 1px solid #ccc;
  color: black;
  background-color: #f1f1f1;
  box-shadow: 0px 0px 5px gainsboro;
}
#featuresDetails .tab button:hover {
  background-color: #42b9c2;
  color: white;
}
#featuresDetails .tab .active {
  background-color: #42b9c2;
  color: white;
}
#featuresDetails .tab .active:hover {
  background-color: #3d9fa7;
  color: white;
}

/* ****************** Tab Content ****************/
#featuresDetails .tabcontent {
  display: flex;
  flex-direction: column;
  width: 80%;
  padding: 6px 12px;
  border: 1px solid #cccccc;
  box-shadow: 0px 0px 5px gainsboro;
  border-top: none;
  background-color: white;
}
#featuresDetails .tabcontent .featuresDetails {
  display: grid;
  grid-template-columns: auto auto;
  padding: 10px;
  margin: 10px auto;
}
#featuresDetails .tabcontent .featuresDetails p {
  text-align: left;
  margin: 0;
  padding: 5px;
}
#featuresDetails .tabcontent .featuresDetails p:nth-child(odd) {
  text-align: right;
}
/********* Responsive Design ********/
@media screen and (max-width: 1000px) {
  #featuresDetails .tab button:hover {
    background-color: #f1f1f1;
    color: black;
  }
  #featuresDetails .tab .active:hover {
    background-color: #42b9c2;
  }
}
@media screen and (min-width: 741px) and (max-width: 850px) {
  #featuresDetails .tab button {
    width: 33.333%;
    padding: 0 10px;
  }
}

@media screen and (max-width: 550px) {
  #featuresDetails .tab button {
    width: 33.333%;
    padding: 0 5px;
    height: 40px;
  }
}
@media screen and (max-width: 400px) {
  #featuresDetails .tab {
    display: grid;
    grid-template-columns: auto auto auto;
  }
  #featuresDetails .tab button {
    width: 100%;
  }
}
</style>
